.carousel {
    display: flex;
    flex-direction: column;
    overflow-x: auto;
    scroll-behavior: smooth;
    background-color: #303030;
    width: 620px;
    max-width: 75vw;
    border: 1px solid rgba(223, 223, 223, 0.16);
    border-radius: 5px;
    margin: 30px;
}

.carousel::-webkit-scrollbar{
  display: none;
}

.carousel .item {
  margin:5px;
  padding: 2px;
  width: 1300px;
  height: 100px;
}

.carousel .item .usuario {
    height: 20px;
    padding-right: 130px;
    display: flex;
    justify-content: space-around;
    color: white;
    align-content: center;
    font-size: 13px;
  }
  
  .perfil1 {
    margin-top:10px;
    margin-left: 12px;
    object-fit: cover;
    border-radius: 50%;
    border: solid 3px red;
  }

  .setaesquerda{
    position: absolute;
    background-color: transparent;
    border: none;
    cursor: pointer;
    transform: translate(10%, 140%);
  }
  
  .setadireita{
    position: absolute;
    background-color: transparent;
    border: none;
    cursor: pointer;
    transform: translate(2350%, 140%);
  }