.wrapperprofile{
    position:relative;
    width:1730px;
    left:43%;
    transform:translateX(-50%);
    background-color: #212121;
}

.wrappercontentProfile{
    display: flex;
    position:relative;
    justify-content:center;
    align-items: center;
    margin-left: 420px;

}
.containerprofileContent{
    display:flex;
    flex-direction:column;
    row-gap:30px;
    padding: 30px;
    margin-right: 50px;
    
}
.wrapperprofileFirst,
.wrapperprofileSecond,
.wrapperprofileThird{
    display:flex;
    align-items:center;
}

.name {
    color: white;
}

.wrapperprofileSecond p{
    color: white;
    font-size: 15px;
}
.wrapper__profileThird p {
    color: white;
    font-size: 15px;
}


.wrapperprofileFirst>.name{
    font-weight: 200;
}

.wrapperprofileFirst>.editar{
    font-size: 14px;
    font-weight: 600;
    margin: 0 10px 4px 10px;
    padding: 5px 9px;
    border-radius: 3px;
    background-color: black;
    color: white;
    border: 1px solid rgb(223, 223, 223);
}
.wrapperprofileSecond{
    width: 355px
}

.wrapperprofileImg{
    width: 150px;
    height: 150px;
    position:absolute;
    left:18%;
