
.post{
    width: 620px;
    background-color: #424242;
    border: 1px solid rgba(223, 223, 223, 0.16);
    border-radius: 5px;
    padding-bottom: 10px;
    margin: 30px;
}

.post__header{
    display: flex;
    align-items: center;
    color: white;
    background-color: #212121;
    padding: 6px;
}

.perfil {
    margin-right: 15px;
    margin-top: 5px;
    border-radius: 50%;
}

.dot {
    margin-left: 430px;
}

.post__image{
    width: 618px;
    padding-bottom: 5px;
}

.post__text{
    margin-left: 10px;
    padding: 5px;
    font-weight: 400;
    color: rgb(170, 164, 164);
}
.icone{
    margin-left: 13px;
    padding: 1px;
}

.like1 {
    position: absolute;
    top: 35%;
    left: 50%;
    transform: translate(-1133%, 1673%);
    opacity: 0;
    z-index: 1000;
}

.like1:hover {
    transition: 0.1s;
    opacity: 1;
}

