* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica,
      Arial, sans-serif;
  }
  
  body {
    color: #262626;
    
    font-size: 14px;
    line-height: 18px;
  }
  
  button {
    font-size: 14px;
    line-height: 18px;
    cursor: pointer;
    border: 1px solid transparent;
  }

  .text__button {
    background-color: transparent;
  }



  
.app__signUp {
    display: flex;
    flex-direction: column;
    margin: 28px 0;
    row-gap: 15px;
}
  
.timeline {
    margin-left: 245px;
    max-width: 500px;
    padding: 20px;
}


  