.app {
    background-color: #212121;
    min-height: 100vh;
}

.app__header {
    position: sticky;
    top: 0;
    z-index: 9999;
    background-color: #424242;
    height: 54px;
    display: flex;
    align-items: center;
    object-fit: contain;
}

.app__headerWrapper {
    max-width: 975px;
    margin: 0 auto;
    margin-top: 10px;
    width: 100%;
    padding: 0 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

.app__headerButtons {
    display: flex;
    column-gap: 15px;
}

.search {
    width: 250px;
    height: 35px;
    margin-left: 190px;
    margin-bottom: 7px;
    border: none;
    border-radius: 7px;
    background-color: #212121;
    font-weight: 100;
    padding: 10px;
}